body {
    max-height: 100vh;
    max-width: 100vw;
    background-color: rgb(163, 63, 63);
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
}

ol, ul {
    padding-left: 30px;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.status {
    margin-bottom: 10px;
}

.square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 1rem;
    font-weight: bold;
    line-height: 34px;
    height: 10vh;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 12vw;
}

.square:focus {
    outline: none;
}

.kbd-navigation .square:focus {
    background: #ddd;
}

.game {
    margin-top: 10%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.game-board {
    margin: auto;
    justify-content: center;
}

.game-info {
    font-weight: bold;
    padding-top: 5%;
    text-align: center;
    margin: auto;
}

p {
    font-weight: bold;
    text-align: center;
}